import React, { Component } from 'react';
import HomeHeader from '../components/HomeHeader';
import ListNav from '../components/ListNav';
import PastEventSection from '../components/PastEventSection';
import LinkArchive from '../components/LinkArchive';
import Event from '../components/Event';
import PastEvent from '../components/PastEvent';
import Footer from '../../../components/Footer';
import { events } from '../../../main/constants/eventsConstants';
import stmLogo from '../../../../images/logo-stm-white.png';
import _ from 'lodash';
class HomePage extends Component {

  constructor() {
    super();

    this.state = {
      loading: false
    }
  }

  componentDidMount() {
    setTimeout(() => this.setState({ loading: true }), 2000);
  }

  _renderEvent = () => {
    return (
      <div>
        {
          events.map((data, index) => (
            <Event key={index} {...data} />
          ))
        }
      </div>
    )
  };
  _renderPastEvent = () => {
    const filteredEvents = _.filter(events, { isArchive: true });
    const firstFiveEvents = _.take(filteredEvents, 5);

    return (
      <div>
        {
          firstFiveEvents.map((data, i) => (
            <PastEvent key={i} {...data} />
          ))
        }
      </div>
    )
  };

  render() {
    const loading = this.state.loading;

    if (loading) {
      return (
        <div className="page home-wrapper">
          <HomeHeader />
          <ListNav />
          {this._renderEvent()}
          <PastEventSection />
          {this._renderPastEvent()}
          <LinkArchive />
          <Footer />
        </div>
      )
    }

    return (
      <div className="page home-wrapper">
        <div className="load-screen">
          <div className="loader">
            <img src={stmLogo} alt="" />
            <p>please wait...</p>
          </div>
        </div>
      </div>
    )
  }
}

export default HomePage;



