import update from 'immutability-helper';
import { actions } from '../constants/eventConstants';
import { initialState } from '../eventInitialState';

export default function eventReducers(state = initialState, action) {
  switch (action.type) {

    case actions.SET_EVENT_ID:
      return update(state, {
        ui: {
          eventId: { $set: action.payload.eventId }
        }
      });

    default:
      return state;
  }
}



