import React from 'react';
import stmLogo from '../../../../images/logo-stm-white.png'

const HomeHeader = () => (
  <div className="header">
    <nav className="nav-bar">
      <img src={ stmLogo } alt="" className="logo"/>
      <a href="https://stmforum.com/" className="btn" target="_blank">go to stmforum.com</a>
    </nav>
    <section className="content">
      <h1 className="title">Meetups <small>2018</small></h1>
      <p className="text">Get together with affiliate marketers, entrepreneurs, and affiliates in your area.
        These meetups offer STM members an opportunity to stay updated with the latest happenings in the
        affiliate marketing industry and network with others.</p>
    </section>
  </div>
);

export default HomeHeader;



