import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import { events } from '../../../main/constants/eventsConstants';
import _ from 'lodash';

class PageNav extends Component {
  static propTypes = {
    eventItem: PropTypes.object,
    events: PropTypes.array
  };

  constructor() {
    super();

    this.state = {
      nextPageUrl: '',
      prevPageUrl: ''
    }
  }

  _getCurrentPage = () => {
    let currentPage = _.findIndex(events, (o) => {
      return o.id === this.props.eventItem.id
    });

    return currentPage;
  }

  _getNextPage = () => {
    let nextPage = this._getCurrentPage();

    return nextPage + 1;
  }

  _getNextPageUrl = () => {
    let nextPage    = this._getNextPage();

    return events[nextPage].id;
  }

  _onClickNextPage = () => {
    this.setState({
      nextPageUrl: this._getNextPageUrl()
    })
  }

  _getPrevPage = () => {
    let prevPage = this._getCurrentPage();

    return prevPage - 1;
  }

  _getPrevPageUrl = () => {
    let prevPage    = this._getPrevPage();

    return events[prevPage].id;
  }

  _onClickPrevPage = () => {
    this.setState({
      prevPageUrl: this._getPrevPageUrl()
    })
  }

  _renderPrevButton = () => {
    if (this._getCurrentPage() !== 0) {
      return (
        <a className="prev"
           href={ `${process.env.PUBLIC_URL}/event/${ this.state.prevPageUrl }` }
           onClick={ () => this._onClickPrevPage() }>
          <i className="icon icon-arrow-left-circle m-r-15"/>
          <p className="text">Previous Meetup</p>
        </a>
      )
    }
    return (
      null
    )
  }

  _renderNextButton = () => {
    if (this._getCurrentPage() < this._checkLimit() - 1) {
      return (
        <a className="next"
           href={ `${process.env.PUBLIC_URL}/event/${ this.state.nextPageUrl }` }
           onClick={ () => this._onClickNextPage() }>
          <p className="text m-r-15">Next Meetup</p>
          <i className="icon icon-arrow-right-circle"/>
        </a>
      )
    }
    return (
      null
    )
  }

  _checkLimit = () => {
    let limit = 0;

    for (const event of events) {
      if (!event.isTBD) {
        limit++;
      }
    }

    return limit
  }

  render() {
    // console.log(this._getCurrentPage())
    return (
      <div className="page-nav">
        <section className="content">
          { this._renderPrevButton() }
          <Link className="btn" to={ `${process.env.PUBLIC_URL}/` }>see all
            events</Link>
          { this._renderNextButton() }
        </section>
      </div>
    )
  }
}

export default PageNav;



