import React, { Component, PropTypes } from 'react';
import '../../../assets/styles/style.scss';

class AppLayout extends Component {

  static propTypes = {
    children: PropTypes.object
  }

  render() {
    return (
      <section className="page-wrapper">
        { this.props.children }
      </section>
    )
  }

}

export default AppLayout;



