import React from 'react';
import stmLogo from '../../images/logo-stm-black.png'

const Footer = () => (
  <div className="footer container-fluid">
    <div className="row">
      <div className="col-md-8 col-xs-12">
        <img className="logo" src={ stmLogo } alt="stm logo"/>
        <p className="text">© 2017 STM Forum. All Rights Reserved. <a
          href="https://stmforum.com/privacy.html" target="_blank">Privacy Policy</a> &nbsp;&nbsp;•&nbsp;&nbsp; Questions? <a
          href="mailto:info@stmforum.com">info@stmforum.com</a></p>
      </div>
      <div className="col-md-4 col-xs-12 text-right">
        <a className="icon" href="https://www.facebook.com/groups/STMMEETUPS/"
           target="_blank"><i className="fa fa-facebook-square"/></a>
        <a className="icon" href="https://twitter.com/stmforum" target="_blank"><i
          className="fa fa-twitter"/></a>
        <a className="icon"
           href="https://www.youtube.com/channel/UCiIGcnuSoTBqiYYs0j4hEpg"
           target="_blank"><i className="fa fa-youtube-play"/></a>
      </div>
    </div>
  </div>
);

export default Footer;



