import React, { Component, PropTypes } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import EventHeader from '../components/EventHeader';
import EventDetails from '../components/EventDetails';
import PageNav from '../components/PageNav';
import Footer from '../../../components/Footer';
import { setEventId } from '../actions/eventActions';
import { eventSelector } from '../selectors/eventSelectors';

class EventPage extends Component {

  static propTypes = {
    params: PropTypes.object,
    eventId: PropTypes.string,
    setEventId: PropTypes.func,
    eventItem: PropTypes.object,
    events: PropTypes.array
  };

  componentWillMount() {
    this.props.setEventId(this.props.params.id);
  }

  render() {
    return (
      <div className="page event-wrapper">
        <EventHeader eventItem={this.props.eventItem}
                     eventId={this.props.eventId}/>
        <EventDetails eventItem={this.props.eventItem}/>
        <PageNav eventItem={this.props.eventItem}/>
        <Footer/>
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  eventId: state.events.ui.eventId,
  eventItem: eventSelector(state)
});

const mapDispatchToProps = (dispatch) => ({
  setEventId: bindActionCreators(setEventId, dispatch)
});

export default connect(mapStateToProps, mapDispatchToProps)(EventPage);



