import React from 'react';
import { Router, Route, IndexRoute, browserHistory } from 'react-router';
import AppLayout from './page/AppLayout';
import HomePage from '../modules/home/page/HomePage';
import Archive from '../modules/archive/page/ArchivePage';
import Event from '../modules/event/page/EventPage';

const Routes = () => (
  <Router onUpdate={() => window.scrollTo(0, 0)} history={ browserHistory }>
    <Route path={ `${process.env.PUBLIC_URL}/` } component={ AppLayout }>
      <IndexRoute component={HomePage} />
      <Route path={ `${process.env.PUBLIC_URL}/archive` } component={ Archive } />
      <Route path={ `${process.env.PUBLIC_URL}/event/:id` } component={ Event } />
    </Route>
  </Router>
);

export default Routes;



