import React, { Component } from 'react';

class LinkArchive extends Component {

  render() {
    return (
      <div className="list-nav text-center link-archive">
        <h2><a href={`${process.env.PUBLIC_URL}/archive`}>View Past Events <i className="fa fa-long-arrow-right"/></a></h2>
      </div>
    )
  }
}

export default LinkArchive;



