import { createSelector } from 'reselect';

const eventsSelector = state => state.events.data.events;
const eventIdSelector = state => state.events.ui.eventId;

export const eventSelector = createSelector(
  [eventsSelector, eventIdSelector],
  (events, eventId) => {
    return events.find(event => event.id === eventId) || {}
  }
);



