import React, { Component, PropTypes } from 'react';
import { Link } from 'react-router';
import classNames from 'classnames';

class PastEvent extends Component {
  static propTypes = {
    isTBD: PropTypes.bool,
    isArchive: PropTypes.bool,
    eventType: PropTypes.string,
    id: PropTypes.string,
    month: PropTypes.string,
    day: PropTypes.string,
    city: PropTypes.string,
    country: PropTypes.string,
    address: PropTypes.string,
    about: PropTypes.string,
    mapLink: PropTypes.string,
    rsvpLink: PropTypes.string,
    logo: PropTypes.string,
    logoLight: PropTypes.string,
    sponsors: PropTypes.array
  };

  _renderEventButton = () => {
    if (this.props.isTBD) {
      return (
        <Link className="btn --disabled">View Event Details</Link>
      )
    }
    return (
      <Link to={`${process.env.PUBLIC_URL}/event/${this.props.id}`}
        className="btn --secondary">View Event Details</Link>
    )
  };

  _renderRsvpButton = () => {
    if (this.props.isTBD || this.props.isArchive) {
      return (
        <a className="btn --disabled m-l-20" target="_blank">RSVP Now</a>
      )
    }
    return (
      <a href={this.props.rsvpLink} className="btn --primary m-l-20"
        target="_blank">RSVP Now</a>
    )
  }

  _renderPartners = () => {
    if (this.props.eventType === 'stm-amc') {
      return (
        <div className="partners --custom text-center">
          {
            this.props.sponsors.map((data, index) => (
              <img key={index} src={data.logoLight} className="logo" />
            ))
          }
        </div>
      )
    }
  };

  _renderThumbnail = () => {
    if (this.props.isTBD) {
      return (
        <div className={classNames({
          thumb: true,
          [`${this.props.id}`]: true
        })}>
          <h3 className="label">{this.props.city}</h3>
          {this._renderPartners()}
        </div>
      )
    }
    return (
      <Link to={`${process.env.PUBLIC_URL}/event/${this.props.id}`}>
        <div className={classNames({
          thumb: true,
          [`${this.props.id}`]: true
        })}>
          <h3 className="label">{this.props.city}</h3>
          {this._renderPartners()}
        </div>
      </Link>
    )
  };

  _getEventText = () => {
    if (!this.props.isTBD) {
      return (
        <p className="text" dangerouslySetInnerHTML={{ __html: this.props.about }} />
      )
    }
    return (
      <p className="text">{this.props.about}</p>
    )
  }

  render() {
    if (this.props.isArchive) {
      return (
        <div className="event container-fluid">
          <div className="row">
            <section className="date col-md-1 col-sm-12">
              <h5 className="month">{this.props.month}</h5>
              <h4 className="day">{this.props.day}</h4>
            </section>
            <section className="col-md-6 col-sm-6">
              {this._renderThumbnail()}
            </section>
            <section className="event-content col-md-5 col-sm-6">
              <h1 className="title">{this.props.city},
                <span>{' ' + this.props.country}</span></h1>
              <a href={this.props.mapLink} className="address block"
                target="_blank">
                <i className="fa fa-map-marker" />{this.props.address}
              </a>
              {this._getEventText()}
              {this._renderEventButton()}
              {this._renderRsvpButton()}
            </section>
          </div>
        </div>
      )
    }
    return null;
  }
}

export default PastEvent;



