import React, { Component, PropTypes } from 'react';
import stmLogo from '../../../../images/logo-stm-white.png';
import { Link } from 'react-router';
import classNames from 'classnames';

class EventHeader extends Component {
  static propTypes = {
    eventItem: PropTypes.object
  };

  render() {
    return (
      <div className={classNames({
        header: true,
        [`${this.props.eventItem.id}`]: true
      })}>
        <nav className="nav-bar">
          <img src={ stmLogo } alt="" className="logo"/>
          <a href="https://stmforum.com/" className="btn" target="_blank">go to
            stmforum.com</a>
        </nav>
        <section className="content">
          <h1 className="title">{ this.props.eventItem.city }</h1>
          <p className="date">{ this.props.eventItem.date }</p>
        </section>
        <section className="social-container">
          <div className="nav-bottom">
            <Link to={ `${process.env.PUBLIC_URL}/` } className="event-list">
              <i className="fa fa-long-arrow-left"/> Back to
              Events List
            </Link>
            <span className="nav-social">
              <span>Share this event:</span>
              <a href={this.props.eventItem.fbLink} target="_blank"><i className="fa fa-facebook-square"/></a>
              {/*<a><i className="fa fa-twitter"/></a>*/}
            </span>
          </div>
        </section>
      </div>
    )
  }
}

export default EventHeader;



